
import { defineComponent } from "vue";
import io from "socket.io-client";
import axios from "axios";

export default defineComponent({
  name: "UserManager",
  data() {
    return {
      newUser: "",
      socket: io(
        `${process.env.VUE_APP_API_URL}:${process.env.VUE_APP_API_PORT}`
      ),
    };
  },
  computed: {
    getUsers() {
      return this.$store.state.userList;
    },
  },
  methods: {
    addNewUser() {
      axios
        .post(
          `http://${process.env.VUE_APP_API_URL}:${process.env.VUE_APP_API_PORT}/api/user/create/`,
          { userName: this.newUser }
        )
        .then((res) => {
          if (res.status === 201) {
            this.socket.emit(
              "TRIGGER_UPDATE_USERS",
              this.$store.state.pairMatrix
            );
          }
        });
    },
    removeUser(id: number) {
      axios
        .put(
          `http://${process.env.VUE_APP_API_URL}:${process.env.VUE_APP_API_PORT}/api/user/remove/`,
          { userId: id }
        )
        .then((res) => {
          if (res.status === 204) {
            this.socket.emit(
              "TRIGGER_UPDATE_USERS",
              this.$store.state.pairMatrix
            );
          }
        });
    },
  },
});
